import { ref } from 'vue';
import { useBaseUrl } from '@/composables/baseUrl';

const headerParams = ref({});
/**
 * Paramtres du composant DSFR Header
 * @returns 
*/
export function useHeaderParams() {

    // Paramtres pour le Header
    headerParams.value = {
        serviceTitle: 'cartes.gouv.fr',
        serviceDescription: 'Le service public des cartes et donnes du territoire',
        logoText: ['rpublique', 'franaise'],
        quickLinks: [
          // hack : doit tre complt pour pouvoir utiliser le slot afterQuickLinks
          {
            label: '',
            to: '/',
            href: '#',
          }
        ],
        afterQuickLinks: [
          {
            title: "Aide",
            icon: "ri-question-fill",
            links :[
              {
                  text: "Questions Frquentes",
                  to: useBaseUrl() + '/aide',
                  icon: "ri-question-mark"
              },
              {
                  text: "Aide",
                  to: useBaseUrl() + '/aide/fr/guides-utilisateur/visualiseur-cartographique/',
                  icon: "ri-book-2-line"
              },
              {
                  text: "Nous contacter",
                  to: useBaseUrl() + '/nous-ecrire',
                  icon: "ri-mail-line"
              }
            ]   
          },  
        {
            title: "Services",
            icon: "ri-grid-fill",
            links :[
              {
                  text: "Explorer les cartes",
                  to: useBaseUrl() + '/cartes',
                  icon: "ri-road-map-line"
              },
              {
                  text: "Rechercher une donne",
                  to: useBaseUrl() + '/catalogue/search',
                  icon: "ri-search-line"
              },
              {
                  text: "Publier une donne",
                  to: useBaseUrl() + '/tableau-de-bord',
                  icon: "ri-database-line"
              },
              // {
              //     text: "Crer une carte",
              //     to: useBaseUrl() + '/creer-une-carte',
              //     icon: "ri-brush-line"
              // },
              {
                  text: "Dcouvrir cartes.gouv",
                  to: useBaseUrl(),
                  icon: "fr-icon-external-link-line",
                  button : true
              }
            ]   
          }       
        ]
    };

    if (import.meta.env.IAM_DISABLE === '0') {
      headerParams.value.afterQuickLinks.push(
        {
          title: "Mon espace",
          icon: "ri-account-circle-fill",
          connexionMenu: true,
          links :[
            {
                text: "Tableau de bord",
                to: useBaseUrl() + '/tableau-de-bord',
                icon: "ri-dashboard-3-line"
            },
            {
                text: "Paramtres du compte",
                to: useBaseUrl() + '/mon-compte',
                icon: "ri-user-line"
            }
          ]   
        });
    }

    return headerParams;
};

